body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

footer {
  font-family: 'Orbitron', sans-serif;
  color: #fff;
  font-size: 0.75em;
}


.directions-title {
  margin: 5rem auto 2rem;
}

.directions {
  font-size: 2.5em;
  line-height: 1.5em;
  font-family: 'Fjalla One', sans-serif;
  color: #fff;
  padding: 15px;
}

.directions::first-letter {
  font-size: 3em;
}

.directions-container {
  border: 1px #fff solid;
  margin: 0 auto 2em;
  width: 50%;
}

.quiz-nav {
  color: #fff;
  font-size: 2em;
  font-family: 'Fjalla One', sans-serif;
  margin-bottom: 1.5em;
}
body {    
  background-color: #720000; 
  background-blend-mode: screen;
  height: 100%; 
  width:100%; 
}

body:after {
  background-image: url(/MarvelMatch/home/static/media/black-panther.346a9d96.jpg);
  background-repeat: no-repeat;
  background-size: cover;
  content: '';
  height: 100%;
  left: 0;
  opacity: .1;
  position: absolute;
  top: 0;
  width: 100%;
  z-index: -1;
}

.wrapper {
  box-sizing: border-box;
  height: 100vh;
  margin: auto;
  position: relative;
  text-align: center;
  width: 100%;
}

.logo-container {
  display: flex;
  flex-direction: column;
}

@keyframes leaves {
  0% {
    transform: scale(2.0);
    -webkit-transform: scale(2.0);
  }
  100% {
    transform: scale(1.0);
    -webkit-transform: scale(1.0);
  }
}

@-webkit-keyframes leaves {
  0% {
    transform: scale(2.0);
    -webkit-transform: scale(2.0);
  }
  100% {
    transform: scale(1.0);
    -webkit-transform: scale(1.0);
  }
}

@-webkit-keyframes fadeIn {
  0% {opacity: 0;}  
  100% {opacity: 1;}
}

@keyframes fadeIn {
  0% {opacity: 0;}  
  100% {opacity: 1;}
}

.fadeIn {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}

@keyframes fadeOut {
  0% {opacity: 1;}
  100% {opacity: 0;}
}

@-webkit-keyframes fadeOut {
  0% {opacity: 1;}
  100% {opacity: 0;}
}

.fadeOut {
  -webkit-animation-name: fadeOut;
  animation-name: fadeOut;}

@-webkit-keyframes fadeInDown {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-20px);
    transform: translateY(-20px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes fadeInDown {
  0% {
    opacity: 0;
    -webkit-transform: translateY(-20px);
    transform: translateY(-20px);
  }

  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

.fadeInDown {
  -webkit-animation-name: fadeInDown;
  animation-name: fadeInDown;
}

.gif { 
  animation: fadeOut 10s forwards;
  background: url(/MarvelMatch/home/static/media/comic-flip.f898c1d6.gif);
  background-repeat: no-repeat;
  background-size: cover;
  height: 100vh;
  position: absolute; 
  top: 0; left: 0;
  width: 100vw;  
  -webkit-animation: fadeOut 10s forwards;
  -moz-animation: fadeOut 10s forwards;
}

.marvel { 
  animation: leaves 10s ease-in-out forwards;
  border: solid 1px #fff;
  color: #fff; 
  -webkit-animation: leaves 10s ease-in-out forwards;
  font-family: 'Fjalla One', sans-serif;
  font-size: 8em; 
  line-height: 1;
  margin: 180px auto 0;
  padding: 15px 10px 0 10px;
  text-align: center;
  width: 500px; 
}

.match { 
  font-family: "Orbitron", sans-serif;
  animation: leaves 10s ease-in-out forwards;
  -webkit-animation: leaves 10s ease-in-out forwards;
  font-weight: bolder;
  color: #fff;
  letter-spacing: 67px;
  text-align: center;
  position: relative;
  margin: 15px 15px 5px 85px;
}

.links-container {
  box-sizing: border-box;
  margin: 5rem auto 5rem;
  opacity: 0;
  -webkit-animation: opacityOn ease-in-out forwards;
          animation: opacityOn ease-in-out forwards;
  -webkit-animation-duration: 100ms;
          animation-duration: 100ms;
  -webkit-animation-delay: 11s;
          animation-delay: 11s;
  text-align: center;
  width: 30%
}

@-webkit-keyframes opacityOn {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}

@keyframes opacityOn {
  0% {
      opacity: 0;
  }
  100% {
      opacity: 1;
  }
}


.landing-nav {
  font-family: 'Fjalla One', sans-serif; 
  margin: 25px auto;
  color: #fff;
  font-size: 2rem;
}

.landing-nav:hover {
  cursor: pointer;
  color: yellow;
}

.link-text {
  color: #fff;
}

@media (max-width: 700px) {
  .marvel {
    width: 60%;
  }

  .match {
    letter-spacing: 64px;
  }
}

@media (max-width: 650px) {
  .marvel {
    font-size: 6em;
  }

  .match {
    letter-spacing: 32px;
    margin: 15px 15px 5px 50px;
  }
}

@media (max-width: 475px) {
  .marvel {
    /*width: 100%;*/
    font-size: 5em;
  }

  .link-text {
    font-size: 1em;
  }
}

@media (max-width: 400px) {
  .marvel {
    padding: 10px 4px 10px 0;
    width: 280px;
    font-size: 5em;
  }

  .match {
    letter-spacing: 30px;
  }

  .link-text {
    font-size: 0.75em;
  }
}
.error-wrapper {
  background: url(/MarvelMatch/home/static/media/groot-404.cc09c099.jpg);
  background-size: cover;
  height: 100vh;
  width: 100vw;
}

.home-link {
  margin: 1.5em;
  font-size: 2em;
}

.warning-container {
  text-align: left;
  width: 35%;
  padding: 1em;
  margin-left: 1.5em
}

h1 {
  font-size: 4.5em;
  font-family: 'Fjalla One', sans-serif;
  color: #fff;
}

.translation {
  font-family: "Orbitron", sans-serif;
  line-height: 1.5em;
  font-size: 1.5em;
  letter-spacing: 1px;
  color: #fff;
}

.error-nav {
  color: #fff;
  font-family: 'Fjalla One', sans-serif;
  text-align: left;
}
.question {
  font-family: 'Fjalla One', sans-serif;
  font-size: 2.5em;
  color: #fff;
}
.question-count {
  font-family: 'Orbitron', sans-serif;
  font-size: 1.5em;
  color: #fff;
}
li {
  list-style-type: none;
  color: #fff;
  font-family: 'Orbitron', sans-serif;
  font-size: 1.5em;
  line-height: 1.5em;
}

.radio-button {
  margin: 10px;
  font-size: 2.5em;
  vertical-align: text-bottom;
}

.radio-button:hover {
  cursor: pointer;
}
.answer-options {
  margin: 2em auto 10em;
  padding: 0;
}

.quiz-container {
  border: 1px #fff solid;
  width: 40%;
  margin: auto;
  height: 25%; 
}


.match-heading {
  margin-top: 0;
}

.result {
  text-align: center;
  margin: 1em auto;
  height: 120%;
  background: #7d0000;
}

.match-name {
  font-size: 2.5em;
  font-family: 'Orbitron', sans-serif;
  color: #fff;
}

.char-description {
  font-family: 'Orbitron', sans-serif;
  font-size: 1em;
  line-height: 1.5em;
  color: #fff;
  width: 75%;
  margin: 1em auto;
}

.bar-graph {
  list-style: none;
  padding: 20px 0 0 0;
  width: 27%;
  margin: auto;
  text-align: center;
}

ul.bar-graph li {
  height: 2.5rem;
  color: #fff;
  font-style: italic;
  font-weight: bolder;
  font-size: 1.5em;
  line-height: 1.5em;
  padding: 0px 20px;
  margin: 5px auto;
}

